<template>
	<HistorySkeleton :dataReady="dataReady" :isBtnAll="limit === 3">
		<template #historyFilter>
			<div  class="history-features__btns">
				<HistoryFilter :dataReady="dataReady" :filters="filters" @data-ready="(bool) => dataReady = bool" @edit-filter="(item)=>filters = item"/>
			</div>
		</template>
		<template #historyItems>
			<HistoryDate v-for="(value, name) in HistoryItems" :key="name" :date="name" class="history-features__wrapper" :dataReady="dataReady">
				<HistoryItem v-for="item in value" :key="item.date" :data="item" :dataReady="dataReady"/>
			</HistoryDate>
		</template>
	</HistorySkeleton>
</template>

<script setup>

import HistoryItem from '@/entities/components/HistoryItem';
import HistoryDate from '@/entities/components/HistoryDate';
import HistorySkeleton from '@/entities/components/HistorySkeleton';
import HistoryFilter from '@/entities/components/HistoryFilter';

import { useStore } from 'vuex';
import {
	ref, defineProps, computed, watch, onMounted,
} from 'vue';
import { useRoute } from 'vue-router';

const props = defineProps({
	limit: {
		type: Number,
		default: 3,
	},
});

const filters = ref(['income', 'expense']);

const dataReady = ref(false);

const route = useRoute();
const store = useStore();
const HistoryItems = computed(() => store.state.historyFeatures.items);
console.log(HistoryItems);
const contractUid = computed(() => store.state.contract?.uid);

const getHistory = async () => {
	console.log('GETGETGET');
	const options = {
		statuses: ['PAYOUT', 'CONFIRMED', 'FORM_SHOWED', 'NEW', 'CANCELED', 'AUTH_FAIL', 'REJECTED', 'DEADLINE_EXPIRED', '3DS_CHECKING'],
		limit: props.limit,
	};
	if (route.path !== '/payment-history') {
		options.contract_uid = contractUid.value;
	}
	await store.dispatch('historyFeatures/getHistoryItemsAction', options);
	dataReady.value = true;
};
onMounted(() => {
	if (contractUid.value) {
		getHistory();
	}
});
watch(contractUid, getHistory);

</script>

<style scoped lang="scss">
@import "historyFeatures";
</style>
